import axios from "axios";
import { Label, Input, Button, Select } from "../components/Form";
import { useForm } from "react-hook-form";
import { t } from "i18next";
import { useState, useEffect } from "react";

// components
import { Page, Section, Container, H2 } from "../App";

export default function Upload() {
  const { handleSubmit } = useForm();

  const [type, setType] = useState("");
  const [city, setCity] = useState("");
  const [region, setRegion] = useState("");
  const [success, setSuccess] = useState(false);
  const [selectFile, setSelectFile] = useState(null);

  const onSubmit = async () => {
    const URL =
      "https://cors-anywhere.herokuapp.com/http://api.positionstack.com/v1/forward?access_key=" +
      process.env.REACT_APP_POSITION_STACK_API_KEY +
      "&query=" +
      city +
      "+" +
      region;

    const response = await axios.get(URL);

    // console.log(response.data.data[0].latitude);
    // console.log(response.data.data[0].longitude);

    const formData = new FormData();
    formData.append("file", selectFile);
    formData.append("longitude", response.data.data[0].longitude);
    formData.append("latitude", response.data.data[0].latitude);
    formData.append("type", type);

    await axios.post(process.env.REACT_APP_SERVER + "/audio", formData);
    setSuccess(true);
  };

  useEffect(() => {
    if (success === true) {
      setTimeout(() => (window.location.href = "../rainmap"), 5000);
    }
  }, [success]);

  return (
    <Page title={`Rainmap |  ${t("upload.upload")}`}>
      {success ? (
        <Section>
          <Container>
            <div className="h-[398px] flex flex-col gap-2">
              <div className="flex flex-col grow gap-2 justify-center items-center">
                <span
                  className="material-symbols-outlined"
                  id="user"
                  style={{
                    filter:
                      "invert(75%) sepia(14%) saturate(1052%) hue-rotate(95deg) brightness(92%) contrast(86%)",
                    fontSize: "36px",
                  }}
                >
                  check_circle
                </span>
                <span className="mt-2">{t("upload.success")}</span>
                <span>{t("upload.thanks")}</span>
              </div>
            </div>
          </Container>
        </Section>
      ) : (
        <Section>
          <H2 name={t("upload.upload-audio")} margin="-4px" />
          <p> {t("upload.contribute")}</p>
          <form onSubmit={handleSubmit(onSubmit)} encType="multipart/form-data">
            <Container>
              <Label for="file" name={t("upload.file")} />
              <Input
                id="file"
                type="file"
                placeholder={t("upload.no-file")}
                required
                change={(e) => setSelectFile(e.target.files[0])}
                style={{
                  whiteSpace: "nowrap",
                  overflow: "hidden",
                  textOverflow: "ellipsis",
                }}
              />
              <div className="grid grid-cols-2 gap-x-4 gap-y-3">
                <Label for="country" name={t("upload.country")} />
                <Label for="city" name={t("upload.region")} />
                <SelectCountry region={region} setRegion={setRegion} />
                <Input
                  id="city"
                  type="text"
                  placeholder={t("upload.region-enter")}
                  required
                  value={city}
                  change={(e) => setCity(e.target.value)}
                  style={{
                    whiteSpace: "nowrap",
                    overflow: "hidden",
                    textOverflow: "ellipsis",
                  }}
                />
              </div>
              <Label for="rain" name={t("upload.rain")} />
              <Select
                id="rain"
                value={type}
                change={(e) => {
                  setType(e.target.value);
                }}
                required
                style={{
                  whiteSpace: "nowrap",
                  overflow: "hidden",
                  textOverflow: "ellipsis",
                }}
              >
                <option value="" disabled hidden selected>
                  {t("upload.rain-select")}
                </option>
                <option value="Soft rain">{t("type.soft-rain")}</option>
                <option value="Hard rain">{t("type.hard-rain")}</option>
                <option value="Hybrid rain">{t("type.hybrid-rain")}</option>
                <option value="Thunder">{t("type.thunder")}</option>
              </Select>
              <div className="mt-6">
                <Button name="Submit" />
              </div>
            </Container>
          </form>
        </Section>
      )}
    </Page>
  );
}

